// Variables and Mixins
// ---------------------------------------------------
@import '../../../style/bootstrap-variables';
@import '../../../style/variables';

// Bootstrap
// ---------------------------------------------------

@import '~bootstrap/scss/functions';

@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/mixins/_breakpoints";

//custom
@import '../../../style/typos-mixins';


.pricing-card-container{
    text-align: center;
    border-radius: $radius_2;
    padding: 2%;
    margin: 0 1vw;
    width: 34%;
    h4{
        padding: 5%;
        @include text-subtitle;
    }
    div{
        padding: 5%;
        @include text-hugebody;
        font-weight: 700;
    }
    li{
        padding: 1%;
        text-align: left;
        @include text-hugebody;
    };
}

// Page pricing

.pricing-card-container-2{
    text-align: left;
    border-radius: $radius_2;
    padding: 2%;
    margin: 0 auto 5vh auto;
    display: flex;
    justify-content: space-between;
    position: relative;
    flex-flow: row wrap;
    h4{
        padding: 2%;
        text-align: center;
        @include text-subtitle;
        width: 100%;
    }
    div{
        padding: 2%;
        @include text-hugebody;
        font-weight: 600;
        width: 45%;
    }
    ul{
        width: 50%;
        padding: 2%;
    }
    li{
        padding: 0 2%;
        text-align: left;
        @include text-hugebody;
    };
}
.blue{
    background: linear-gradient(0deg, rgba(20, 108, 218, 0.67), rgba(20, 108, 218, 0.67)),url('/images/spectnaz_bg.png');
    color: $white;
}
.white{
    color: $primary;
    background-color: $white;
}